const HighlightBox = styled.div`
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.05) 0%, rgba(118, 75, 162, 0.05) 100%);
  border-left: 4px solid #667eea;
  padding: 20px;
  border-radius: 8px;
  margin: 20px 0;
`;

const ChartContainer = styled.div`
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
`;

const RadarContainer = styled.div`
  position: relative;
  min-height: 800px;
  margin: 20px 0;

  .radar-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
  }

  .dimension-card {
    position: absolute;
    width: 300px;
    z-index: 2;

    //  - 
    &.dimension-s {
      top: -10px;
      left: 50%;
      transform: translateX(-50%);
    }

    //  - 
    &.dimension-a {
      top: 12%;
      right: 5%;
      transform: translateY(0);
    }

    //  - 
    &.dimension-g {
      bottom: 12%;
      right: 5%;
      transform: translateY(0);
    }

    //  - 
    &.dimension-r {
      bottom: 12%;
      left: 5%;
      transform: translateY(0);
    }

    //  - 
    &.dimension-e {
      top: 12%;
      left: 5%;
      transform: translateY(0);
    }
  }

  // 
  @media (max-width: 1200px) {
    min-height: 900px;

    .dimension-card {
      width: 280px;

      &.dimension-s {
        top: -20px;
      }

      &.dimension-a {
        top: 10%;
        right: 2%;
      }

      &.dimension-g {
        bottom: 5%;
        right: 2%;
      }

      &.dimension-r {
        bottom: 5%;
        left: 2%;
      }

      &.dimension-e {
        top: 10%;
        left: 2%;
      }
    }
  }

  @media (max-width: 768px) {
    min-height: auto;
    position: static;

    .radar-center {
      position: static;
      transform: none;
      margin-bottom: 30px;
    }

    .dimension-card {
      position: static;
      transform: none !important;
      width: 100%;
      margin-bottom: 20px;

      &.dimension-s,
      &.dimension-a,
      &.dimension-g,
      &.dimension-r,
      &.dimension-e {
        top: auto;
        left: auto;
        right: auto;
        bottom: auto;
      }
    }
  }
`;


